import React, { useState } from 'react';
import styles from './carousel.module.css';
import { useMotionValue, motion, useDragControls, useSpring } from 'framer-motion';

export default function Carousel({ children, width, unit }) {
  // here we extracted the carousel functionality into its own hook
  // const { translate, items, setAction } = useTransition(width, children);
  const x = useSpring(0);
  const handleNext = () => x.set(-(((x.get() + 1) % children.length) * width));
  const handlePrev = () => x.set(-((x.get() - 1) % children.length) * width);
  const dragControls = useDragControls();
  // const x = useMotionValue(-(width * index));
  console.log(x);
  return (
    <div
      className={styles.parent}
      style={{
        width: `${width}${unit}`,
      }}
    >
      <div className={styles.container}>
        <motion.div
          className={styles.inner}
          style={{
            width: `${width * children.length}${unit}`,
            // transform: `translateX(-${width * index}${unit})`,
            // transition: 'transform 1s ease',
            x,
          }}
          drag="x"
          dragControls={dragControls}
          dragConstraints={{ left: 0, right: 0 }}
        >
          {children.map((item, i) => (
            <motion.div
              key={i}
              className={styles.item}
              style={{
                width: `${width}${unit}`,
                // pointerEvents: 'none',
              }}
              onMouseDown={(e) => dragControls.start(e, { snapToCursor: false })}
            >
              {item}
            </motion.div>
          ))}
        </motion.div>
      </div>
      <div className={styles.controls}>
        <button className={styles.next} onClick={handleNext}>
          Next
        </button>
        <button className={styles.prev} onClick={handlePrev}>
          Prev
        </button>
      </div>
    </div>
  );
}
